.clock-counter {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    color: #7894FF; /* light purple */
    font-family: "Genica", sans-serif;
    font-weight: 600;
    margin: 0 2rem; 
    transition: opacity 2s ease-in;
}
.clock-timer {
    font-size: 3.4rem;
    top: 0;
    left: 0;
    width: 100%;
    height: 70%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    object-fit: cover;
    position: absolute;
    transition: opacity 2s ease-in;
}
.clock-unit {
    font-size: 1.8rem;
    position: relative;
    transition: opacity 2s ease-in;
}

.progress-bar {
    position: relative;
}
.progress-bar svg {
    position: relative;
    width: 150px;
    height: 150px;
    z-index: 1000;
}
.progress-bar svg circle { 
    width: 100%;
    height: 100%;
    fill: none;
    stroke: #7894FF; /* light purple */
    stroke-width: 10;
    -webkit-transform: translate(5px, 5px);
            transform: translate(5px, 5px);
    transition-duration: 1s;
}
.progress-bar svg circle:nth-child(2) {
    stroke: #FF9D72; /* light orange */
    stroke-dasharray: 440; 
}
